.input-container {
    position: relative;
    margin: 5rem 0;
    width: 75%;
}

.input-container input[type="text"] {
    font-size: 20px;
    width: 100%;
    border: none;
    border-bottom: 2px solid #ccc;
    padding: 5px;
    background-color: transparent;
    outline: none;
    text-align: left;
}

.input-container .label {
    position: absolute;
    top: 0;
    left: 0;
    color: #ccc;
    transition: all 0.3s ease;
    pointer-events: none;
}

.input-container input[type="text"]:focus~.label,
.input-container input[type="text"]:valid~.label {
    top: -20px;
    font-size: 16px;
    color: #333;
}

.input-container .underline {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: #333;
    transform: scaleX(0);
    transition: all 0.3s ease;
}

.input-container input[type="text"]:focus~.underline,
.input-container input[type="text"]:valid~.underline {
    transform: scaleX(1);
}


/* ! ADD PRODUCT */

.add_product {
    margin: 10vh auto;
    width: 80%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    user-select: none;
}

.add_product-container {
    text-align: left;
    width: 50%;
}

.add_product-container h2 {
    color: #000;
    font-size: 42px;
    font-weight: 700;
    text-align: left;
    margin-bottom: 3rem;
}

.add_product-container label {
    color: #000;
    font-weight: 500;
    font-size: 1.25rem;
}

.add_product-container label input {
    display: block;
    margin-bottom: 1.5rem;
    margin-top: 0.25rem;
    width: 75%;
    text-align: left;
    padding: 9px 12px;
    font-size: 1rem;
    border-radius: 6px;
    border: 1px solid #707070;
}

.products_categories {
    margin-top: 0.1rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    max-width: 90%;
    flex-wrap: wrap;
}

.products_categories .product-category-selector {
    background: rgba(0, 0, 0, 0.12);
    padding: 8px 10px;
    margin-right: 6px;
    margin-top: 6px;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 400;
    font-size: 1.1rem;
    transition: 0.175s ease-in-out;
}

.products_categories .product-category-selector.category-selected {
    background: rgba(0, 0, 0, 0.95);
    color: #fff;
    transition: 0;
}

.form__buttons {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
}

.form__cancel,
.add {
    line-height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 12px;
    width: 160px;
    height: 48px;
    outline: none;
    border: 1px solid #000;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
}

.form__cancel {
    background-color: #fff;
}

.add {
    background-color: #000;
    color: #fff;
    margin-left: 12px;
}

.add_product-image {
    object-fit: contain;
    object-position: center;
    width: 40%;
    height: 75%;
    user-select: none;
}