.products {
    background-color: rgb(238, 239, 255);
    padding: 10vh 0;
    margin: 5vh 0;
    .products__list-container {
        margin: auto;
        width: 90%;
        display: flex;
        align-items: center;
        background-color: rgb(255, 255, 255);
        color: #000;
        border-radius: 14px;
        flex-wrap: wrap;
        .products__nav {
            color: #001C30;
            line-height: calc(1.5rem * 1.4);
            font-size: 1.5rem;
            text-transform: uppercase;
            font-weight: 800;
            padding: 1.25rem 0.25rem;
            width: 100%;
            justify-content: space-between;
            display: flex;
            flex-direction: row;
            text-align: left;
            .products_category {
                width: calc(85%/3);
                &:last-of-type {
                    width: 15%;
                }
            }
            .expiration-date {
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                .arrow-icon {
                    margin-top: 0.21rem;
                    margin-left: 0.125rem;
                }
            }
        }
        .product__row {
            font-weight: 500;
            line-height: calc(1.15rem * 1.25);
            font-size: 1.15rem;
            padding: 1rem 0.25rem;
            width: 100%;
            justify-content: space-between;
            display: flex;
            align-items: center;
            flex-direction: row;
            text-align: left;
            border-top: 1px solid rgb(190, 190, 190);
            li {
                width: calc(85%/3);
                &:last-of-type {
                    width: 15%;
                    display: flex;
                    justify-content: center;
                }
            }
            .proudctName-row {
                text-transform: capitalize;
            }
            .category-row {
                margin: 0 auto;
                width: 37.5%;
                display: flex;
                align-items: center;
                justify-content: space-around;
                .cathegory-thumbnail {
                    height: 35px;
                    width: 35px;
                    user-select: none;
                }
            }
            .expirationDate-row {
                letter-spacing: -0.25px;
                word-spacing: -1px;
                font-size: 1.05rem;
            }
        }
    }
}

.expired {
    color: #ae0303;
    text-transform: uppercase;
    font-weight: 500;
}

.button {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: rgb(20, 20, 20);
    border: none;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.164);
    cursor: pointer;
    transition-duration: .3s;
    overflow: hidden;
    position: relative;
}

.svgIcon {
    width: 12px;
    transition-duration: .3s;
}

.svgIcon path {
    fill: white;
}

.button:hover {
    width: 45%;
    border-radius: 50px;
    transition-duration: .3s;
    background-color: #ae0303;
    align-items: center;
}

.button:hover .svgIcon {
    width: 50px;
    transition-duration: .3s;
    transform: translateY(57.5%);
}

.button::before {
    position: absolute;
    top: -22.5px;
    content: "Delete";
    color: white;
    transition-duration: .3s;
    font-size: 2px;
}

.button:hover::before {
    font-size: 13px;
    opacity: 1;
    transform: translateY(30px);
    transition-duration: .3s;
}